import React from 'react';
import { useHistory } from "react-router-dom";
import { useMediaQuery } from 'react-responsive';

import goGreen from './MapBiomesImages/go-green.jpg';
import Description from './MapBiomesImages/Description/Description.jsx';
import { MAP, MAPMini } from '../../../constants/MAPS.js';
import '../../../commonStyles/toFlexStyle.scss';
import './MapBiomes.scss';

import ImageMapper from 'react-image-mapper';
import BiomesOfBrazilMap from './MapBiomesImages/representativeBiomesImages/Biomes_of_Brazil.png';
import BiomesOfBrazilMapMini from './MapBiomesImages/representativeBiomesImages/Biomes_of_Brazil_Mini.png';

import TheAmazonRainforest from './MapBiomesImages/representativeBiomesImages/TheAmazonRainforest.jpg';
import TheCaatinga from './MapBiomesImages/representativeBiomesImages/TheCaatinga.jpg';
import TheAtlanticForest from './MapBiomesImages/representativeBiomesImages/TheAtlanticForest.jpg';
import ThePantanal from './MapBiomesImages/representativeBiomesImages/ThePantanal.jpg';
import ThePampa from './MapBiomesImages/representativeBiomesImages/ThePampa.jpg';
import TheCerrado from './MapBiomesImages/representativeBiomesImages/TheCerrado.jpg';

export default function MapBiomes() {

   const [currentImageBiome, setCurrentImageBiome] = React.useState('');
   const [currentNameBiome, setCurrentNameBiome] = React.useState('');
   const history = useHistory();

   const isDesktopOrLaptop = useMediaQuery({
      query: '(min-device-width: 1224px)'
   })

   React.useEffect(() => {
      document.title = `Biomes of Brazil`;
   });

   const chooseBiome = (area) => {
      let link = '/' + area.name;
      history.push(link, { from: '/' });
   }

   const hideImageBiome = (area) => {
      setCurrentImageBiome('');
      setCurrentNameBiome('');
   }

   const showImageBiome = (area) => {
      switch (area.name) {
         case 'The Caatinga':
            setCurrentImageBiome(TheCaatinga);
            setCurrentNameBiome('The Caatinga');
            break;
         case 'The Amazon Rainforest':
            setCurrentImageBiome(TheAmazonRainforest);
            setCurrentNameBiome('The Amazon Rainforest');
            break;
         case 'The Atlantic Forest':
            setCurrentImageBiome(TheAtlanticForest);
            setCurrentNameBiome('The Atlantic Forest');
            break;
         case 'The Pantanal':
            setCurrentImageBiome(ThePantanal);
            setCurrentNameBiome('The Pantanal');
            break;
         case 'The Pampa':
            setCurrentImageBiome(ThePampa);
            setCurrentNameBiome('The Pampa');
            break;
         case 'The Cerrado':
            setCurrentImageBiome(TheCerrado);
            setCurrentNameBiome('The Cerrado');
            break;
         default:
            break;
      }
   }

   let isCurrentBiome = currentNameBiome;
   return (
      <div>
         <Description />
         <div className={'toFlex flexDir'} >
            <div id='allBiomes'>
               <h1>Map of Brazilian Biomes</h1>
               <ImageMapper src={isDesktopOrLaptop ? BiomesOfBrazilMap : BiomesOfBrazilMapMini}
                  map={isDesktopOrLaptop ? MAP : MAPMini}
                  onClick={chooseBiome}
                  onMouseEnter={showImageBiome}
                  onMouseLeave={hideImageBiome}
               />
            </div>

            {isCurrentBiome ? (
               <div id='imageCurrentBiome' >
                  <h1>{currentNameBiome}</h1>
                  <img src={currentImageBiome} alt={currentNameBiome} />
               </div>
            ) : isDesktopOrLaptop ? (
               <div id='imageCurrentBiome' >
                  <h1>Save Nature</h1>
                  <img src={goGreen} alt='Calling to save nature' />
               </div>
            ) : null
            }

         </div>
      </div>
   )
}


