import React from 'react';
import { useMediaQuery } from 'react-responsive';

import '../../../../../commonStyles/sectionsStyles.scss';
import butterflyGreen from '../../../../../commonStyles/decorate/9-butterfly-png-image.png';
import '../../../../../commonStyles/decorate/decorateStyles.scss';

import AmazonRacerunner from './CerradoFaunaImages/Amazon_Racerunner_male.jpg';
import ColuberCaspius from './CerradoFaunaImages/Coluber_caspius.jpg';
import HarpyhaliaetusCoronatus from './CerradoFaunaImages/Harpyhaliaetus_coronatus.jpg';
import AnodorhynchusHyacinthinus from './CerradoFaunaImages/Anodorhynchus_hyacinthinus.jpg';
import ChrysocyonBrachyurus from './CerradoFaunaImages/ChrysocyonBrachyurus.jpg';

export default function CerradoFauna() {

   const isDesktopOrLaptop = useMediaQuery({
      query: '(min-device-width: 1224px)'
   })

   React.useEffect(() => {
      document.title = 'Brazil | The Cerrado | Fauna';
   })

   return <main id="main">
      <h1>Fauna of the Cerrado<img src={butterflyGreen} alt='butterfly' className='butterfly' /></h1>

      <p>The insects of the Cerrado are relatively understudied. A yearlong survey of the Cerrado at one reserve in Brazil found that the orders <a href='https://en.wikipedia.org/wiki/Beetle'>Coleoptera</a>, <a href='https://en.wikipedia.org/wiki/Hymenoptera'>Hymenoptera</a>, <a href='https://en.wikipedia.org/wiki/Fly'>Diptera</a> and <a href='https://en.wikipedia.org/wiki/Termite'>Isoptera</a> accounted for 89.5% of all captures. The Cerrado also supports a high density (up to 4000 per hectare) of the nests of leaf cutter ants, which are also very diverse. Along with termites, leaf cutter ants are the primary herbivores of the Cerrado and play an important role in consuming and decomposing organic matter, as well as constituting an important food source to many other animal species. The highest diversity of galling insects (insects that build galls) in the world is also found in the Cerrado, with the most species found at the base of the Serro do Cipó in southeast Brazil.</p>

      <p>The Cerrado has a high diversity of vertebrates; 150 amphibian species, 120 reptile species, 837 bird species, and 161 mammal species have been recorded. Lizard diversity is generally thought to be relatively low in the Cerrado compared to other areas like <a href='https://en.wikipedia.org/wiki/Caatinga'><em>caatinga</em></a> or lowland rainforest although one recent study found 57 species in one cerrado area with the high diversity driven by the availability of open habitat. <a href='https://en.wikipedia.org/wiki/Ameiva_ameiva'><em>Ameiva ameiva</em></a> is among the largest lizards found in the Cerrado and is the most important lizard predator where it is found in the Cerrado. There is a relatively high diversity of snakes in the Cerrado (22–61 species, depending on site) with <a href='https://en.wikipedia.org/wiki/Colubridae'>Colubridae</a> being the richest family. The open nature of the <em>cerrado</em> vegetation most likely contributes to the high diversity of snakes. Information about Cerrado amphibians is extremely limited, although the Cerrado probably has a unique assemblage of species with some endemic to the region.</p>

      <p>The frog species Physalaemus nattereri (posterior view shown) is found in the open cerrado, but not in adjacent gallery forests.
         Most birds found in the Cerrado breed there although there are some Austral migrants (breed in temperate South America and winter in the Amazon basin) and Nearctic migrants (breed in temperate North America and winter in the Neotropics) that pass through. Most breeding birds in the Cerrado are found in more closed canopy areas like gallery forests although 27% of the birds breed only in open habitats and 21% breed in either open or closed habitats. Many of the birds in the Cerrado, especially those found in closed forest, are related to species from the Atlantic rainforest and also the Amazon rainforest. The <a href='https://en.wikipedia.org/wiki/Chaco_eagle'>crowned solitary eagle</a>, <a href='https://en.wikipedia.org/wiki/Hyacinth_macaw'>hyacinth macaw</a>, <a href='https://en.wikipedia.org/wiki/Toco_toucan'>toco toucan</a>, <a href='https://en.wikipedia.org/wiki/Buff-necked_ibis'>buff-necked ibis</a>, <a href='https://en.wikipedia.org/wiki/Dwarf_tinamou'>dwarf tinamou</a>, and <a href='https://en.wikipedia.org/wiki/Brazilian_merganser'>Brazilian merganser</a> are examples of birds found in the Cerrado.</p>

      <p>Gallery forests serve as primary habitat for most of the mammals in the Cerrado, having more water, being protected from fires that sweep the landscape and having a more highly structured habitat. Eleven mammal species are endemic to the Cerrado. Notable species include large herbivores like the <a href='https://en.wikipedia.org/wiki/South_American_tapir'>Brazilian tapir</a> and <a href='https://en.wikipedia.org/wiki/Pampas_deer'>Pampas deer</a> and large predators like the <a href='https://en.wikipedia.org/wiki/Maned_wolf'>maned wolf</a>, <a href='https://en.wikipedia.org/wiki/Cougar'>cougar</a>, <a href='https://en.wikipedia.org/wiki/Jaguar'>jaguar</a>, <a href='https://en.wikipedia.org/wiki/Giant_otter'>giant otter</a>, <a href='https://en.wikipedia.org/wiki/Ocelot'>ocelot</a> and < a href='https://en.wikipedia.org/wiki/Jaguarundi'>jaguarundi</a>. Although the diversity is much lower than in the adjacent Amazon and Atlantic Forest, several species of monkeys are present, including <a href='https://en.wikipedia.org/wiki/Black-striped_capuchin'>black-striped capuchin</a>, < a href='https://en.wikipedia.org/wiki/Black_howler'>black howler monkey</a> and <a href='https://en.wikipedia.org/wiki/Black-tufted_marmoset'>black-tufted marmoset.</a></p>

      <div className='toFlexImages'>
         <div className='toCenterFigcaptionTextAlign'><img src={AmazonRacerunner} alt='Ameiva ameiva' height={isDesktopOrLaptop ? 200 : 145} /><figcaption>Ameiva ameiva</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={ColuberCaspius} alt='Caspian whipsnake (Dolichophis caspius)' height={200} /><figcaption>Caspian whipsnake (Dolichophis caspius)</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={HarpyhaliaetusCoronatus} alt='Chaco eagle' height={200} /><figcaption>Chaco eagle</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={AnodorhynchusHyacinthinus} alt='Hyacinth macaw' height={200} /><figcaption>Hyacinth macaw</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={ChrysocyonBrachyurus} alt='Maned wolf' height={200} /><figcaption>Maned wolf</figcaption></div>
      </div>
      <br />
   </main>
}



