import React from 'react';
import { useMediaQuery } from 'react-responsive';

import typicalPantanal from './typicalPantanal.jpg';
import landscapePantanal from './landscapePantanal.jpg';

export default function ThePantanalDescr() {
   const isDesktopOrLaptop = useMediaQuery({
      query: '(min-device-width: 1224px)'
   })

   return (
      <article id="description">
         <p>The <b>Pantanal</b> is a <a href='https://en.wikipedia.org/wiki/Natural_region'>natural region</a> encompassing the world's largest tropical wetland area, and the world's largest flooded grasslands. It is located mostly within the Brazilian state of Mato Grosso do Sul, but it extends into Mato Grosso and portions of Bolivia and Paraguay. It sprawls over an area estimated at between 140,000 and 195,000 square kilometres (54,000 and 75,000 sq mi). Various subregional ecosystems exist, each with distinct hydrological, geological and <a href='https://en.wikipedia.org/wiki/Ecology'>ecological</a> characteristics; up to 12 of them have been defined.</p>

         <p>Roughly 80% of the Pantanal floodplains are submerged during the rainy seasons, nurturing a biologically diverse collection of aquatic plants and helping to support a dense array of animal species.</p>

         <p>The name "Pantanal" comes from the Portuguese word pântano, meaning wetland, bog, swamp, quagmire or marsh. By comparison, the <a href='https://en.wikipedia.org/wiki/Brazilian_Highlands'>Brazilian highlands</a> are locally referred to as the planalto, plateau or, literally, high plain.</p>

         <div className='toFlexImages'>
            <div className='toCenterFigcaptionTextAlign'><img src={landscapePantanal} alt='Landscape of the Pantanal.' height={isDesktopOrLaptop ? 200 : 180} /><figcaption>Landscape of the Pantanal.</figcaption></div>
            <div className='toCenterFigcaptionTextAlign'><img src={typicalPantanal} alt='Typical Pantanal scenery' height={200} /><figcaption>Typical Pantanal scenery.</figcaption></div>
         </div>

      </article>
   )
}







