import React from 'react';
import { NavLink } from 'react-router-dom';
import { useMediaQuery } from 'react-responsive';

import Description from '../MapBiomesImages/Description/Description.jsx';

import Jaguar from './AmazonSections/Jaguar-Belize-Zoo.jpg';
import River from './AmazonSections/River_in_the_Amazon_rainforest.jpg';

export default function TheAmazonBiome() {

   const isDesktopOrLaptop = useMediaQuery({
      query: '(min-device-width: 1224px)'
   })

   React.useEffect(() => {
      document.title = 'Biomes of Brazil | The Amazon';
   })

   return (
      <main id='main'>
         <h1>The Amazon Biome</h1>
         <Description />
         <h2>Choose the section you are interested in:</h2>
         <div className='toFlexImages'>
            <div className='toCenterFigcaptionFitContent sectionRepresent'><NavLink exact to='The%20Amazon%20Rainforest/AmazonFauna' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}><img src={Jaguar} alt='A jaguar' height={200} /><p>Fauna</p></NavLink></div>
            <div className='toCenterFigcaptionFitContent sectionRepresent'><NavLink exact to='The%20Amazon%20Rainforest/AmazonFlora' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}><img src={River} alt='A river in the Amazon rainforest' height={200} /><p>Flora</p></NavLink></div>
         </div>
         <div className='videoContainer'>
            <h3>Watch video (in portugues)</h3>
            <iframe width={isDesktopOrLaptop ? 560 : 270} height={isDesktopOrLaptop ? 315 : 250} src="https://www.youtube.com/embed/sVdsHjsAR-s" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
         </div>
         <br />
      </main>
   )
}