import React from 'react';
import { NavLink } from 'react-router-dom';

import './Contents.scss'

export default class Contents extends React.Component {
   render() {
      return (
         <main id="main">
            <h1>Contents</h1>

            <div id='notTextAlign'>
               <ul>
                  <li><NavLink exact to='/The Amazon Rainforest' activeStyle={{ color: '#32CD32' }} style={{ color: '#006400' }}>The Amazon biome</NavLink>
                     <ul>
                        <li><NavLink exact to='/The Amazon Rainforest/AmazonFauna' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}>Fauna of the Amazon</NavLink></li>
                        <li><NavLink exact to='/The Amazon Rainforest/AmazonFlora' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}>Flora of the Amazon</NavLink></li>
                     </ul>
                  </li>
                  <li><NavLink exact to='' activeStyle={{ color: '#32CD32' }} style={{ color: '#006400' }}>Atlantic Forest</NavLink>
                     <ul>
                        <li><NavLink exact to='' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}>Atlantic Forest Fauna</NavLink></li>
                        <li><NavLink exact to='' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}>Atlantic Forest Flora</NavLink></li>
                     </ul>
                  </li>
                  <li><NavLink exact to='/The Caatinga' activeStyle={{ color: '#32CD32' }} style={{ color: '#006400' }}>Caatinga</NavLink>
                     <ul>
                        <li><NavLink exact to='/The Caatinga/CaatingaFauna' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}>Caatinga Fauna</NavLink></li>
                        <li><NavLink exact to='/The Caatinga/CaatingaFlora' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}>Caatinga Flora</NavLink></li>
                     </ul>
                  </li>
                  <li><NavLink exact to='/The Cerrado' activeStyle={{ color: '#32CD32' }} style={{ color: '#006400' }}>Cerrado</NavLink>
                     <ul>
                        <li><NavLink exact to='/The Cerrado/CerradoFauna' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}>Cerrado Fauna</NavLink></li>
                        <li><NavLink exact to='/The Cerrado/CerradoFlora' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}>Cerrado Flora</NavLink></li>
                     </ul>
                  </li>
                  <li><NavLink exact to='/The Pampa' activeStyle={{ color: '#32CD32' }} style={{ color: '#006400' }}>Pampa</NavLink>
                     <ul>
                        <li><NavLink exact to='/The Pampa/PampaFauna' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}>Pampa Fauna</NavLink></li>
                        <li><NavLink exact to='/The Pampa/PampaFlora' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}>Pampa Flora</NavLink></li>
                     </ul>
                  </li>
                  <li><NavLink exact to='/The Pantanal' activeStyle={{ color: '#32CD32' }} style={{ color: '#006400' }}>Pantanal</NavLink>
                     <ul>
                        <li><NavLink exact to='/The Pantanal/PantanalFauna' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}>Pantanal Fauna</NavLink></li>
                        <li><NavLink exact to='/The Pantanal/PantanalFlora' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}>Pantanal Flora</NavLink></li>
                     </ul>
                  </li>
               </ul>
            </div>
         </main >
      )
   }
}
