import React from 'react';
import { NavLink } from 'react-router-dom';
import { useMediaQuery } from 'react-responsive';
import Description from '../MapBiomesImages/Description/Description.jsx';

import Rhea from './PampaSections/Greater_rhea.JPG';
import Herbe from './PampaSections/Herbe_Pampa_FR_2008.jpg';

import '../../../../commonStyles/video.scss';

export default function ThePampa() {
   const isDesktopOrLaptop = useMediaQuery({
      query: '(min-device-width: 1224px)'
   })

   React.useEffect(() => {
      document.title = 'Biomes of Brazil | The Pampa';
   })

   return (
      <main id='main'>
         <h1>The Pampa</h1>
         <Description />
         <h2>Choose the section you are interested in:</h2>
         <div className='toFlexImages'>
            <div className='toCenterFigcaptionFitContent sectionRepresent'><NavLink exact to='/The Pampa/PampaFauna' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}><img src={Rhea} alt='Rhea' height={isDesktopOrLaptop ? 200 : 180} /><p>Fauna</p></NavLink></div>
            <div className='toCenterFigcaptionFitContent sectionRepresent'><NavLink exact to='/The Pampa/PampaFlora' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}><img src={Herbe} alt='Pampas grass' height={isDesktopOrLaptop ? 200 : 180} /><p>Flora</p></NavLink></div>
         </div>
         <div className='videoContainer'>
            <h3>Watch video (in portugues)</h3>
            <iframe width={isDesktopOrLaptop ? 560 : 270} height={isDesktopOrLaptop ? 315 : 250} src="https://www.youtube.com/embed/-Xf8BEQtk3Y" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
         </div>
         <br />
      </main>
   )
}




