import React from 'react';
import '../../../../../commonStyles/sectionsStyles.scss';

import Delfin from './AmazonFaunaImages/Amazonas_Flussdelfin.jpg';
import Bicho from './AmazonFaunaImages/Bicho.jpg';
import Capybara from './AmazonFaunaImages/Capybara.JPG';
import Hoatzin from './AmazonFaunaImages/Hoatzin.jpg';
import Macaw from './AmazonFaunaImages/Macaw.jpg';
import Ramphastos from './AmazonFaunaImages/Ramphastos_vitellinus.jpg';

export default function AmazonFauna() {

   React.useEffect(() => {
      document.title = 'Brazil | The Amazon | Fauna';
   })

   return <main id="main">
      <h1>Fauna of the Amazon rainforest</h1>

      <p>The <a href='https://en.wikipedia.org/wiki/Amazon_rainforest'>Amazon rainforest</a> is a species-rich biome in which thousands of species live, including animals found nowhere else in the world. To date, there is at least 40,000 different kinds of <a href='https://en.wikipedia.org/wiki/Plant'>plants</a>, 427 kinds of <a href='https://en.wikipedia.org/wiki/Mammal'>mammals</a>, 1,300 kinds of <a href='https://en.wikipedia.org/wiki/Bird'>birds</a>, 378 kinds of <a href='https://en.wikipedia.org/wiki/Reptile'>reptiles</a>, more than 400 kinds of <a href='https://en.wikipedia.org/wiki/Amphibian'>amphibians</a>, and around 3,000 <a href='https://en.wikipedia.org/wiki/Freshwater_fish'>freshwater fish</a> are living in Amazon.</p>

      <p>The Amazon rainforest covers 6.7 million square kilometre, accounting for over 40% of the planet’s remaining tropical forests. Since 2000, rainfall has declined across 69% of the Amazon. The <a href='https://en.wikipedia.org/wiki/World_Wide_Fund_for_Nature'>WWF</a> estimates that 27% Amazon will be without trees if deforestation continues at its current rate.</p>

      <h3>Birds</h3>
      <p>There are more than 1,500 kinds of birds living in the Amazon. Like <a href='https://en.wikipedia.org/wiki/Hummingbird'>hummingbirds</a>, <a href='https://en.wikipedia.org/wiki/Channel-billed_toucan'>Channel-billed toucan</a>, <a href='https://en.wikipedia.org/wiki/Hoatzin'>hoatzin</a> and <a href='https://en.wikipedia.org/wiki/Macaw'>macaws</a>. In one sites of Peruvian Amazon, there are around 575 kinds of birds are found in only a 5,500 hectare section.</p>

      <p>In November to March, some birds will migrate from Central American to South American topics. Other are exactly found in Amazon.</p>

      <p>Most of the birds are living in undercover dense of Amazon forest because of they are looking for insects from rainforest floor to the <a href='https://en.wikipedia.org/wiki/Canopy_(biology)'>canopy</a>. Some prefer flying insects, or fruits and flowers, like <a href='https://en.wikipedia.org/wiki/Harpy_eagle'>harpy eagle</a> will capture other mammals and reptiles and birds.</p>

      <h3>Mammals</h3>
      <p>There are about 427 different kinds of <a href='https://en.wikipedia.org/wiki/Mammal'>mammals</a> living in Amazon, with most of them being <a href='https://en.wikipedia.org/wiki/Bat'>bats</a> and <a href='https://en.wikipedia.org/wiki/Rodent'>rodents</a>, including the largest rodent in the world, the <a href='https://en.wikipedia.org/wiki/Capybara'>capybara</a>, which can weigh up to 200 pounds (91 kg).</p>

      <p>In Amazon forest mammals are on both ground , trees and the air , the jungle are fill with a diversity of different species. When people arrive Amazon forest , they can look at the tallest trees and they can find <a href='https://en.wikipedia.org/wiki/Sloth'>sloths</a>. Sloths are one of the Amazon most common mammals.</p>

      <div className='toFlexImages'>
         <div className='toCenterFigcaptionFitContent'><img src={Delfin} alt='The Amazon river dolphin is a species endemic to the Amazon' height={200} /><figcaption>The Amazon river dolphin is a species endemic to the Amazon</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Bicho} alt='Sloth' height={200} /><figcaption>Sloth</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Capybara} alt='Capybara' height={200} /><figcaption>Capybara</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Hoatzin} alt='Hoatzin' height={200} /><figcaption>Hoatzin</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Macaw} alt='Macaw' height={200} /><figcaption>Macaw</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Ramphastos} alt='Channel-billed toucan' height={200} /><figcaption>Channel-billed toucan</figcaption></div>
      </div>
      <br />
   </main>
}

