import React from 'react';
import { useMediaQuery } from 'react-responsive';
import '../../../../../commonStyles/sectionsStyles.scss';

import Carnauba from './CaatingaFloraImages/Carnauba.jpg';
import Crassula from './CaatingaFloraImages/Crassula_perfoliata.jpg';
import Mandacaru from './CaatingaFloraImages/Mandacaru.jpg';
import SplitAloe from './CaatingaFloraImages/Split_Aloe.jpg';
import thumbnail from './CaatingaFloraImages/thumbnail.jpg';

export default function CaatingaFlor() {

   const isDesktopOrLaptop = useMediaQuery({
      query: '(min-device-width: 1224px)'
   })

   React.useEffect(() => {
      document.title = 'Brazil | The Caatinga | Flora';
   })

   return <main id="main">
      <h1>Flora of the Caatinga</h1>

      <p>The Caatinga does not correspond to a single type of vegetation, but rather a broad mosaic. Nonetheless, all vegetative structure is adapted to the xeric climate. <a href='https://en.wikipedia.org/wiki/Succulent_plant'>Succulent</a> and <a href='https://en.wikipedia.org/wiki/Crassulaceae'>crassulaceous</a> species dominate; non-succulents exhibit small, firm leaves and intense branching at the base, akin to shrubs. Palm stands usually contain <a href='https://en.wikipedia.org/wiki/Copernicia_prunifera'>carnauba</a> or <a href='https://en.wikipedia.org/wiki/Attalea_speciosa'>babacu</a> palms, but occasionally <a href='https://en.wikipedia.org/wiki/Astrocaryum_vulgare'>tucuma</a> and <a href='https://en.wikipedia.org/wiki/Acrocomia_aculeata'>macauba</a>.</p>

      <p>The Caatinga has enough endemic species to constitute a floristic province.</p>

      <p>Most authors divide the Caatinga into two different subtypes: dry ("sertao") and humid ("agreste"), but categorizations vary to as many as eight different vegetative regimes.</p>

      <div className='toFlexImages'>
         <div className='toCenterFigcaptionTextAlign'><img src={Carnauba} alt='Carnauba palm' height={isDesktopOrLaptop ? 200 : 170} /><figcaption>Carnauba palm</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Crassula} alt='Crassulaceae' height={200} /><figcaption>Crassulaceae</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Mandacaru} alt='Cereus jamacaru' height={200} /><figcaption>Cereus jamacaru</figcaption></div>
         <div className='toCenterFigcaptionFitContent'><img src={SplitAloe} alt='Succulent plants have thickened stems, or leaves, such as this Aloe.' height={200} /><figcaption>Succulent plants have thickened stems, or leaves, such as this Aloe.</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={thumbnail} alt='Acrocomia aculeata' height={200} /><figcaption>Acrocomia aculeata</figcaption></div>
      </div>
      <br />
   </main>
}

