import React from 'react';
import { useMediaQuery } from 'react-responsive';

import '../../../../../commonStyles/sectionsStyles.scss';

import Opossum from './PampaFaunaImages/Didelphis_albiventris.jpg';
import Fox from './PampaFaunaImages/Lycalopex_gymnocercus.jpg';
import Maguari from './PampaFaunaImages/Maguari_Stork.jpg';
import Screamer from './PampaFaunaImages/Southern_screamer.JPG';
import Deer from './PampaFaunaImages/Venado-Campo.jpg';
import guineaPig from './PampaFaunaImages/Wildmeerschweinchen.jpg';

export default function PampaFauna() {

   const isDesktopOrLaptop = useMediaQuery({
      query: '(min-device-width: 1224px)'
   })

   React.useEffect(() => {
      document.title = 'Brazil | The Pampa | Fauna';
   })

   return <main id="main">
      <h1>Fauna of the Pampa</h1>

      <p>Human activity has caused major changes to the wildlife of the Pampas. Species such as the <a href='https://en.wikipedia.org/wiki/Puma_(genus)'>puma</a>, <a href='https://en.wikipedia.org/wiki/Rhea_(bird)'>rhea</a>, and <a href='https://en.wikipedia.org/wiki/Pampas_deer'>Pampas deer</a> have <a href='https://en.wikipedia.org/wiki/Habitat_destruction'>lost their habitats</a> especially due to the spread of <a href='https://en.wikipedia.org/wiki/Agriculture'>agriculture</a> and <a href='https://en.wikipedia.org/wiki/Ranch'>ranching</a>.</p>

      <p>Herbivores of the pampas are the Pampas deer, <a href='https://en.wikipedia.org/wiki/Gray_brocket'>gray brocket</a>, <a href='https://en.wikipedia.org/wiki/Plains_viscacha'>plains viscacha</a>, <a href='https://en.wikipedia.org/wiki/Brazilian_guinea_pig'>Brazilian guinea pig</a>, <a href='https://en.wikipedia.org/wiki/Southern_mountain_cavy'>southern mountain cavy</a> and <a href='https://en.wikipedia.org/wiki/Coypu'>coypu</a>. The biggest predator of the region is the puma followed by the <a href='https://en.wikipedia.org/wiki/Maned_wolf'>maned wolf</a>, <a href='https://en.wikipedia.org/wiki/Pampas_fox'>Pampas fox</a>, <a href='https://en.wikipedia.org/wiki/Geoffroy%27s_cat'>Geoffroy's cat</a>, <a href='https://en.wikipedia.org/wiki/Lesser_grison'>lesser grison</a> as well as the omnivorous <a href='https://en.wikipedia.org/wiki/White-eared_opossum'>white-eared opossum</a> and <a href='https://en.wikipedia.org/wiki/Molina%27s_hog-nosed_skunk'>Molina's hog-nosed skunk</a>.</p>

      <p>Bird species of the pampas are <a href='https://en.wikipedia.org/wiki/Ruddy-headed_goose'>ruddy-headed goose</a>, <a href='https://en.wikipedia.org/wiki/Pampas_meadowlark'>pampas meadowlark</a>, <a href='https://en.wikipedia.org/wiki/Hudsonian_godwit'>hudsonian godwit</a>, <a href='https://en.wikipedia.org/wiki/Maguari_stork'>maguari stork</a>, <a href='https://en.wikipedia.org/wiki/White-faced_ibis'>white-faced ibis</a>, <a href='https://en.wikipedia.org/wiki/White-winged_coot'>white-winged coot</a>, <a href='https://en.wikipedia.org/wiki/Southern_screamer'>southern screamer</a>, <a href='https://en.wikipedia.org/wiki/Dot-winged_crake'>dot-winged crake</a>, <a href='https://en.wikipedia.org/wiki/Curve-billed_reedhaunter'>curve-billed reedhaunter</a>, <a href='https://en.wikipedia.org/wiki/Burrowing_owl'>burrowing owl</a> and the <a href='https://en.wikipedia.org/wiki/Rhea_(bird)'>rhea</a>.</p>

      <p>Invasive species include the <a href='https://en.wikipedia.org/wiki/European_hare'>European hare</a>, <a href='https://en.wikipedia.org/wiki/Wild_boar'>wild boar</a> and <a href='https://en.wikipedia.org/wiki/House_sparrow'>house sparrow</a>.</p>

      <div className='toFlexImages'>
         <div className='toCenterFigcaptionTextAlign'><img src={Opossum} alt='White-eared opossum' height={isDesktopOrLaptop ? 200 : 160} /><figcaption>White-eared opossum</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Fox} alt='Pampas fox' height={isDesktopOrLaptop ? 200 : 180} /><figcaption>Pampas fox</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Maguari} alt='Maguari stork' height={200} /><figcaption>Maguari stork</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Screamer} alt='Southern screamer' height={isDesktopOrLaptop ? 200 : 180} /><figcaption>Southern screamer</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Deer} alt='Pampas deer' height={200} /><figcaption>Pampas deer</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={guineaPig} alt='Brazilian guinea pig' height={200} /><figcaption>Brazilian guinea pig</figcaption></div>
      </div>
      <br />
   </main>
}


