import React from 'react';
import { NavLink } from 'react-router-dom';
import { useMediaQuery } from 'react-responsive';
import Description from '../MapBiomesImages/Description/Description.jsx';

import Tamandua from './PantanalSections/Tamandua_Pantanal.jpg';
import Handroanthus from './PantanalSections/Tabebuia_heptaphylla.jpg';

export default function ThePantanal() {
   const isDesktopOrLaptop = useMediaQuery({
      query: '(min-device-width: 1224px)'
   })

   React.useEffect(() => {
      document.title = 'Biomes of Brazil | The Pantanal';
   })

   return (
      <main id='main'>
         <h1>The Pantanal</h1>
         <Description />
         <h2>Choose the section you are interested in:</h2>
         <div className='toFlexImages'>
            <div className='toCenterFigcaptionFitContent sectionRepresent'><NavLink exact to='/The Pantanal/PantanalFauna' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}><img src={Tamandua} alt='Southern tamandua' height={isDesktopOrLaptop ? 200 : 180} /><p>Fauna</p></NavLink></div>
            <div className='toCenterFigcaptionFitContent sectionRepresent'><NavLink exact to='/The Pantanal/PantanalFlora' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}><img src={Handroanthus} alt='Handroanthus heptaphyllus' height={200} /><p>Flora</p></NavLink></div>
         </div>
         <div className='videoContainer'>
            <h3>Watch video (in portugues)</h3>
            <iframe width={isDesktopOrLaptop ? 560 : 270} height={isDesktopOrLaptop ? 315 : 250} src="https://www.youtube.com/embed/L-bw0GMiO7o" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
         </div>
         <br />
      </main>
   )
}


