import React from 'react';

import Caatinga from './Caatinga.jpg';
import Mandacaru from './Mandacaru.jpg'
import CaatingaMap from './CaatingaMap.png'


export default class TheCaatingaDescr extends React.Component {
   render() {
      return (
         <article>
            <p><b>Caatinga</b> is a type of subtropical vegetation, and an <a href='https://en.wikipedia.org/wiki/Ecoregion'>ecoregion</a> characterized by this vegetation in interior <a href='https://en.wikipedia.org/wiki/Northeast_Region,_Brazil'>northeastern Brazil</a>. The name "Caatinga" is a <a href='https://en.wikipedia.org/wiki/Tupian_languages'>Tupi</a> word meaning "white forest" or "white vegetation" (caa = forest, vegetation, tinga = white).</p>

            <p>The Caatinga is a xeric shrubland and thorn forest, which consists primarily of small, thorny trees that shed their leaves seasonally. Cacti, thick-stemmed plants, thorny brush, and arid-adapted grasses make up the ground layer. Most vegetation experiences a brief burst of activity during the three-month long rainy season.</p>

            <p>Caatinga falls entirely within earth's <a href='https://en.wikipedia.org/wiki/Tropics'>tropical zone</a> and is one of 6 major ecoregions of Brazil. It covers 850,000 km², nearly 10% of Brazil's territory. It is home to 26 million people and over 2000 species of plants, fish, reptiles, amphibians, birds, and mammals.</p>

            <div className='toFlexImages'>
               <div className='toCenter'><div className='toCenterFigcaptionTextAlign'><img src={Caatinga} alt='Caatinga Camp Florestation' height={200} /><figcaption>Caatinga</figcaption></div></div>
               <div className='toCenter'><div className='toCenterFigcaptionTextAlign'><img src={Mandacaru} alt='Cereus jamacaru Plant' height={200} /><figcaption>Cereus jamacaru</figcaption></div></div>
               <div className='toCenter'><div className='toCenterFigcaptionTextAlign'><img src={CaatingaMap} alt='Map of the Caatinga ecoregion' height={200} /><figcaption>Map of the Caatinga ecoregion.</figcaption></div></div>
            </div>
         </article>
      )
   }
}