import React from 'react';
import { useMediaQuery } from 'react-responsive';

import '../../../../../commonStyles/sectionsStyles.scss';

import blackTamarin from './AtlanticFaunaImages/blackTamarin.jpg';
import curassow from './AtlanticFaunaImages/curassow.jpg';
import goldenTamarin from './AtlanticFaunaImages/goldenTamarin.jpg';
import jacamar from './AtlanticFaunaImages/jacamar.jpg';
import tanager from './AtlanticFaunaImages/tanager.jpg';
import woolySpider from './AtlanticFaunaImages/woolySpider.jpg';

export default function AtlanticFlora() {

   const isDesktopOrLaptop = useMediaQuery({
      query: '(min-device-width: 1224px)'
   })

   React.useEffect(() => {
      document.title = 'Brazil | The Atlantic Forest | Fauna';
   })

   return <main id="main">
      <h1>Fauna of the Atlantic Forest</h1>
      <p>Around 2,200 <a href='https://en.wikipedia.org/wiki/Species'>species</a> of <a href='https://en.wikipedia.org/wiki/Bird'>birds</a>, <a href='https://en.wikipedia.org/wiki/Mammal'>mammals</a>, <a href='https://en.wikipedia.org/wiki/Reptile'>reptiles</a>, and <a href='https://en.wikipedia.org/wiki/Amphibian'>amphibians</a> live in the Atlantic Forest, including nearly 200 bird species found nowhere else. Of the 26 species of small <a href='https://en.wikipedia.org/wiki/Primate'>primates</a>, most are only found there. These include the golden-headed lion tamarin (<em>L. chrysomelas</em>), black-faced lion tamarin (<em>L. caissara</em>), black lion tamarin (<em>L. chrysopygus</em>), and the highly endangered golden lion tamarin (<em>Leontopithecus rosalia</em>).</p>

      <p>Other mammal species include the muriqui or woolly spider <a href='https://en.wikipedia.org/wiki/Monkey'>monkeys</a> (<em>Brachyteles arachnoides</em>) and the maned <a href='https://en.wikipedia.org/wiki/Sloth'>sloth</a> (<em>Bradypus torquatus</em>). Birds include the red-necked tanager (<em>Tangara cyanocephala</em>), the red-billed currasow (<em>Crax blumenbachii</em>), seven-colored tanager (<em>Tanagara fastuosa</em>), blue-bellied <a href='https://en.wikipedia.org/wiki/Parrot'>parrot</a> (Triclaria malachitacea), and the three-toed jacamar (<em>Jacamaralcyon tridactyla</em>).</p>

      <div className='toFlexImages'>
         <div className='toCenterFigcaptionFitContent'><img src={blackTamarin} alt='The Black-faced lion tamarin' height={200} /><figcaption>The black-faced lion tamarin</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={curassow} alt='The red-billed currasow' height={200} /><figcaption>The red-billed currasow</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={goldenTamarin} alt='The Golden-headed lion tamarin' height={200} /><figcaption>The golden-headed lion tamarin</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={jacamar} alt='The three-toed jacamar' height={isDesktopOrLaptop ? 200 : 180} /><figcaption>The three-toed jacamar</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={tanager} alt='The red-necked tanager' height={200} /><figcaption>The red-necked tanager</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={woolySpider} alt='The muriqui or woolly spider monkey' height={200} /><figcaption>The muriqui or woolly spider monkey</figcaption></div>
      </div>
      <br />
   </main>
}
