import React from 'react';
import { useMediaQuery } from 'react-responsive';

import '../../../../../commonStyles/sectionsStyles.scss';

import Passion from './AmazonFloraImages/Amazon-Rainforest-Plants.jpg';
import Cacao from './AmazonFloraImages/cacao.jpg';
import Coffee from './AmazonFloraImages/fresh.jpg';
import Heliconia from './AmazonFloraImages/helikonie.jpg';
import Monkey from './AmazonFloraImages/MonkeyBrushVine.jpg';
import Bromelia from './AmazonFloraImages/plant.jpg';
import Rubber from './AmazonFloraImages/resin.jpg';
import Lily from './AmazonFloraImages/water-lily.jpg';
import Orchid from './AmazonFloraImages/wild.jpg';

export default function AmazonFlora() {

   const isDesktopOrLaptop = useMediaQuery({
      query: '(min-device-width: 1224px)'
   })

   React.useEffect(() => {
      document.title = 'Brazil | The Amazon | Flora';
   })
   return <main id="main">
      <p>The <a href='https://en.wikipedia.org/wiki/Amazon_rainforest'>Amazon</a> is home to as many as 80,000 plant species from which more than 40,000 species play a critical role in regulating the global climate and sustaining the local water cycle. But the richness of species is one thing, and abundance another. While there may be many species in <a href='https://en.wikipedia.org/wiki/Tropics'>tropical</a> rainforests, these often exist in low numbers over large areas.</p>

      <p>Amazon plants and trees play critical roles in regulating the global climate and sustaining the local water cycle. The forests they form are home to the huge variety of animals found in the Amazon.</p>

      <p>But their greatest riches yet may be the compounds they produce, some of which are used for medicine and agriculture. For Amazon people, both <a href='https://en.wikipedia.org/wiki/Indigenous_peoples_in_Brazil'>indigenous</a> and recent arrivals, plants are a food source and raw matter for non-timber forest products.</p>
      <div className='toFlexImages'>
         <div className='toCenterFigcaptionTextAlign'><img src={Passion} alt='Passion flower, Passiflora edulis' height={isDesktopOrLaptop ? 200 : 180} /><figcaption>Passion flower, Passiflora edulis</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Cacao} alt='Cacao, Theobroma cacao' height={isDesktopOrLaptop ? 200 : 180} /><figcaption>Cacao, Theobroma cacao</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Coffee} alt='Coffee Plant, Coffea arabica' height={isDesktopOrLaptop ? 200 : 180} /><figcaption>Coffee Plant, Coffea arabica</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Heliconia} alt='Heliconia, Heliconia latispatha' height={isDesktopOrLaptop ? 200 : 180} /><figcaption>Heliconia, Heliconia latispatha</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Monkey} alt='Monkey Brush Vines, Combretum rotundifolium' height={isDesktopOrLaptop ? 200 : 220} /><figcaption>Monkey Brush Vines, Combretum rotundifolium</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Bromelia} alt='Bromelia, Bromeliaceae' height={isDesktopOrLaptop ? 200 : 150} /><figcaption>Bromelia, Bromeliaceae</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Rubber} alt='Rubber Tree, Hevea brasiliensis' height={isDesktopOrLaptop ? 200 : 180} /><figcaption>Rubber Tree, Hevea brasiliensis</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Orchid} alt='Orchid, Orchidaceae' height={200} /><figcaption>Orchid, Orchidaceae</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Lily} alt='Giant Water Lily, Victoria Amazonica' height={isDesktopOrLaptop ? 200 : 180} /><figcaption>Giant Water Lily, Victoria Amazonica</figcaption></div>
      </div>
      <br />
   </main>

}

