import React from 'react';
import { useMediaQuery } from 'react-responsive';

import '../../../../../commonStyles/sectionsStyles.scss';
import './CaatingaSections.scss';

import Anodorhynchus from './CaatingaFaunaImages/Anodorhynchus_leari.jpg';
import Dasyprocta from './CaatingaFaunaImages/Dasyprocta.jpg';
import Oryzomys from './CaatingaFaunaImages/Oryzomys.jpg';
import Pseudoseisura from './CaatingaFaunaImages/Pseudoseisura_cristata.jpg';
import Woodcreeper from './CaatingaFaunaImages/Woodcreeper.jpg';

export default function CaatingaFauna() {

   const isDesktopOrLaptop = useMediaQuery({
      query: '(min-device-width: 1224px)'
   })

   React.useEffect(() => {
      document.title = 'Brazil | The Caatinga | Fauna';
   })

   return <main id="main">
      <h1>Fauna of the Caatinga</h1>
      <p>The Caatinga is home to nearly 50 <a href='https://en.wikipedia.org/wiki/Endemism'>endemic</a> species of birds, including Lear's macaw (<em>Anodorhynchus leari</em>), <a href='https://en.wikipedia.org/wiki/Spix%27s_macaw'>Spix's macaw</a> (<em>Cyanopsitta spixii</em>),  <a href='https://en.wikipedia.org/wiki/Moustached_woodcreeper'>moustached woodcreeper</a> (<em>Xiphocolaptes falcirostris</em>),  <a href='https://en.wikipedia.org/wiki/Caatinga_parakeet'>Caatinga parakeet</a>, <a href='https://en.wikipedia.org/wiki/Caatinga_antwren'>Caatinga antwren</a>,  <a href='https://en.wikipedia.org/wiki/Sao_Francisco_black_tyrant'>Sao Francisco black tyrant</a> and <a href='https://en.wikipedia.org/wiki/Caatinga_cacholote'>Caatinga cacholote</a>.</p>

      <p style={{ textAlign: 'left' }}>Endemic mammal species include:</p>
      <ul >
         <li>eleven rodents - <a href='https://en.wikipedia.org/wiki/Caatinga_vesper_mouse'>Caatinga vesper</a>mouse, <a href='https://en.wikipedia.org/wiki/Red-nosed_mouse'><em>Wiedomys pyrrhorhinos</em></a>, <a href='https://en.wikipedia.org/wiki/Yonenaga%27s_Atlantic_spiny_rat'><em>Trinomys yonenagae</em></a>, <a href='https://en.wikipedia.org/wiki/White-spined_Atlantic_spiny_rat'><em>Trinomys albispinus</em></a> minor, <em>Trinomys albispinus sertonius</em>, <a href='https://en.wikipedia.org/wiki/Karimi%27s_fat-tailed_mouse_opossum'><em>Thylamys karimii</em></a>, <a href='https://en.wikipedia.org/wiki/Agouti'><em>Dasyprocta</em></a>, <a href='https://en.wikipedia.org/wiki/Oryzomys'><em>Oryzomys</em></a>, <a href='https://en.wikipedia.org/wiki/Oxymycterus'><em>Oxymycterus</em></a> and <a href='https://en.wikipedia.org/wiki/Rhipidomys'><em>Rhipidomys</em></a></li>
         <li>one primate - <a href='https://en.wikipedia.org/wiki/Callicebus_barbarabrownae'><em>Callicebus barbarabrownae</em></a></li>
         <li>two bats - <a href='https://en.wikipedia.org/wiki/Vieira%27s_long-tongued_bat'><em>Xeronycteris</em></a> <a href='https://en.wikipedia.org/wiki/Chiroderma'><em>vieirai and Chiroderma</em></a></li>
      </ul>

      <div className='toFlexImages'>
         <div className='toCenterFigcaptionTextAlign'><img src={Anodorhynchus} alt="Lear's macaw" height={200} /><figcaption>Lear's macaw</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Dasyprocta} alt='Agouti' height={isDesktopOrLaptop ? 200 : 170} /><figcaption>Agouti</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Oryzomys} alt='Oryzomys' height={200} /><figcaption>Oryzomys</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Pseudoseisura} alt='Caatinga cacholote' height={isDesktopOrLaptop ? 200 : 170} /><figcaption>Caatinga cacholote</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Woodcreeper} alt='Moustached woodcreeper' height={200} /><figcaption>Moustached woodcreeper</figcaption></div>
      </div>
      <br />
   </main>
}

