import React from 'react';
import { NavLink } from 'react-router-dom';
import { useMediaQuery } from 'react-responsive';

import Description from '../MapBiomesImages/Description/Description.jsx';

import Bird from './AtlanticSections/Bird.jpg';
import flor from './AtlanticSections/flor.jpg';

export default function TheAmazonRainforest() {
   const isDesktopOrLaptop = useMediaQuery({
      query: '(min-device-width: 1224px)'
   })

   React.useEffect(() => {
      document.title = 'Biomes of Brazil | The Atlantic Forest';
   })

   return (
      <main id='main'>
         <h1>The Atlantic forest</h1>
         <Description />
         <h2>Choose the section you are interested in:</h2>
         <div className='toFlexImages'>
            <div className='toCenterFigcaptionFitContent sectionRepresent'><NavLink exact to='/The Atlantic Forest/AtlanticFauna' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}><img src={Bird} alt='Myiornis auricularis in the Atlantic forest of the state biological reserve of Aguaí' height={isDesktopOrLaptop ? 200 : 180} /><p>Fauna</p></NavLink></div>
            <div className='toCenterFigcaptionFitContent sectionRepresent'><NavLink exact to='/The Atlantic Forest/AtlanticFlora' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}><img src={flor} alt='red flor' height={isDesktopOrLaptop ? 200 : 180} /><p>Flora</p></NavLink></div>
         </div>
         <div className='videoContainer'>
            <h3>Watch video (in portugues)</h3>
            <iframe width={isDesktopOrLaptop ? 560 : 270} height={isDesktopOrLaptop ? 315 : 250} src="https://www.youtube.com/embed/e8xD16e3rAk" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
         </div>
         <br />
      </main>
   )
}
