import React from 'react';
import { useMediaQuery } from 'react-responsive';

import AmazonManausForest from './Amazon_Manaus_forest.jpg';
import AmazonBiomeMap from './Amazon_biome_Map.png';

export default function TheAmazonBiome() {
   const isDesktopOrLaptop = useMediaQuery({
      query: '(min-device-width: 1224px)'
   })
   return (
      <article id="description">
         <p>The Amazon biome (Portuguese: Bioma Amazônia) contains the Amazon rainforest, an area of <a href="https://en.wikipedia.org/wiki/Tropical_rainforest">tropical rainforest</a>, and other ecoregions that cover most of the <a href='https://en.wikipedia.org/wiki/Amazon_basin'>Amazon basin</a> and some adjacent areas to the north and east.</p>

         <p><a href='https://en.wikipedia.org/wiki/Biome'>The biome</a> contains blackwater and whitewater flooded forest, lowland and montane terra firme forest, bamboo and palm forest, savanna, sandy heath and alpine tundra. Some areas are threatened by deforestation for timber and to make way for pasture or soybean plantations.</p>

         <div className='toFlexImages'>
            <div className='toCenterFigcaptionTextAlign'><img src={AmazonManausForest} alt='Amazon rainforest' height={isDesktopOrLaptop ? 200 : 110} /><figcaption>Amazon rainforest, near Manaus, Brazil</figcaption></div>
            <div className='toCenterFigcaptionFitContent'><img src={AmazonBiomeMap} alt='Amazon Biome Map' height={200} /><figcaption>Map of the Amazon rainforest ecoregions as delineated by the WWF in white and the Amazon drainage basin in blue.</figcaption></div>
         </div>
      </article>
   )
}