import React from 'react';
import './Description.scss';

import TheAmazonRainforestDescr from './TheAmazonBiomeDescr/TheAmazonBiomeDescr.jsx';
import TheAtlanticForestDescr from './TheAtlanticForestDescr/TheAtlanticForestDescr.jsx';
import TheCaatingaDescr from './TheCaatingaDescr/TheCaatingaDescr.jsx';
import TheCerradoDescr from './TheCerradoDescr/TheCerradoDescr.jsx';
import ThePampaDescr from './ThePampaDescr/ThePampaDescr.jsx';
import ThePantanalDescr from './ThePantanalDescr/ThePantanalDescr.jsx';

export default class Description extends React.Component {
   render() {
      let descr = window.location.pathname;
      switch (descr) {
         case "/The%20Amazon%20Rainforest":
            return <TheAmazonRainforestDescr />
         case "/The%20Atlantic%20Forest":
            return <TheAtlanticForestDescr />
         case "/The%20Caatinga":
            return <TheCaatingaDescr />
         case "/The%20Cerrado":
            return <TheCerradoDescr />
         case "/The%20Pampa":
            return <ThePampaDescr />
         case "/The%20Pantanal":
            return <ThePantanalDescr />
         default:
            return (
               <article id="description">
                  <p>There are six terrestrial ecosystem(biomes) in Brazil.</p>
                  <p>Choose one on the map to learn more</p>
               </article>
            )
      }
   }
}


