import './animate.scss';
export let timer;
export function animate() {
   document.querySelector('#root').insertAdjacentHTML('beforebegin', '<div id="unit"></div>');
   function getRandomInt(min, max) {
      min = Math.ceil(min);
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min) + min); //The maximum is exclusive and the minimum is inclusive
   }

   let heightWindowSeen = document.documentElement.clientHeight;
   let widthWindowSeen = document.documentElement.clientWidth;
   let unit = document.querySelector('#unit');

   const obj = {
      greenSnakeToLeft: { x: -6, y: -66, mov: 48, h: -12, v: 0, left: widthWindowSeen - 35, top: getRandomInt(30, heightWindowSeen) },
      greenSnakeToRight: { x: -12, y: -115, mov: 48, h: 12, v: 0, left: 0, top: getRandomInt(10, heightWindowSeen - 30) },
      blueSnakeToLeft: { x: -150, y: -66, mov: 48, h: -12, v: 0, left: widthWindowSeen - 35, top: getRandomInt(30, heightWindowSeen) },
      blueSnakeToRight: { x: -156, y: -115, mov: 48, h: 12, v: 0, left: 0, top: getRandomInt(30, heightWindowSeen - 30) },
      yellowSnakeToLeft: { x: -293, y: -66, mov: 48, h: -12, v: 0, left: widthWindowSeen - 35, top: getRandomInt(30, heightWindowSeen) },
      yellowSnakeToRight: { x: -299, y: -115, mov: 48, h: 12, v: 0, left: 0, top: getRandomInt(30, heightWindowSeen - 30) },
      graySnakeToLeft: { x: -438, y: -66, mov: 48, h: -12, v: 0, left: widthWindowSeen - 35, top: getRandomInt(30, heightWindowSeen) },
      graySnakeToRight: { x: -444, y: -115, mov: 48, h: 12, v: 0, left: 0, top: getRandomInt(30, heightWindowSeen - 30) },
      greenLizzardToLeft: { x: -6, y: -260, mov: 48, h: -12, v: 0, left: widthWindowSeen - 35, top: getRandomInt(30, heightWindowSeen) },
      greenLizzardToRight: { x: -12, y: -308, mov: 48, h: 12, v: 0, left: 0, top: getRandomInt(30, heightWindowSeen - 30) },
      blueLizzardToLeft: { x: -150, y: -260, mov: 48, h: -12, v: 0, left: widthWindowSeen - 35, top: getRandomInt(30, heightWindowSeen) },
      blueLizzardToRight: { x: -156, y: -308, mov: 48, h: 12, v: 0, left: 0, top: getRandomInt(30, heightWindowSeen - 30) },
      yellowLizzardToLeft: { x: -293, y: -260, mov: 48, h: -12, v: 0, left: widthWindowSeen - 35, top: getRandomInt(30, heightWindowSeen) },
      yellowLizzardToRight: { x: -299, y: -308, mov: 48, h: 12, v: 0, left: 0, top: getRandomInt(30, heightWindowSeen - 30) },
      grayLizzardToLeft: { x: -438, y: -260, mov: 48, h: -12, v: 0, left: widthWindowSeen - 35, top: getRandomInt(30, heightWindowSeen) },
      grayLizzardToRight: { x: -444, y: -308, mov: 48, h: 12, v: 0, left: 0, top: getRandomInt(30, heightWindowSeen - 30) },
   }

   const colors = ['green', 'blue', 'yellow', 'gray'];
   const animals = ['Snake', 'Lizzard'];
   const directions = ['ToRight', 'ToLeft'];

   let pos;

   function chooseAnimal() {
      let color = colors[getRandomInt(0, colors.length)];
      let animal = animals[getRandomInt(0, animals.length)];
      let direction = directions[getRandomInt(0, directions.length)];
      let data = obj[color + animal + direction];
      setValues(data)
   }
   chooseAnimal()

   function setValues(data) {
      unit.style.left = data.left + 'px';
      unit.style.top = data.top + 'px';
      pos = data.x;
      timer = setInterval(() => go(data), 100)
   }

   function go(data) {
      if (pos > data.x - 49) pos -= 48;
      else pos = data.x;
      unit.style.backgroundPosition = `${pos}px ${data.y}px`;
      transfer(data.h, data.v)
   }

   function transfer(h, v) {
      if (parseInt(unit.style.left) + 30 >= widthWindowSeen
         || parseInt(unit.style.left) < 0
      ) {
         clearInterval(timer);
         chooseAnimal();
      } else {
         unit.style.left = parseInt(unit.style.left) + h + 'px';
      }
   }

   function reportWindowSize(e) {
      heightWindowSeen = document.documentElement.clientHeight;
      widthWindowSeen = document.documentElement.clientWidth;
   }
   window.addEventListener('resize', reportWindowSize, { passive: true })
}