import React from 'react';
import { useMediaQuery } from 'react-responsive';

import SerraGerall from './SerraGeral.jpg';
import MapPampas from './MapPampas.png';

export default function ThePampaDescr() {
   const isDesktopOrLaptop = useMediaQuery({
      query: '(min-device-width: 1224px)'
   })
   return (
      <article id="description">
         <p>The Pampas (from the Quechua: pampa, meaning "plain") are fertile <a href='https://en.wikipedia.org/wiki/South_America'>South American</a> lowlands that cover more than 1,200,000 square kilometres (460,000 sq mi) and include the Argentine provinces of Buenos Aires, La Pampa, Santa Fe, Entre Ríos, and Córdoba; all of Uruguay; and Brazil's southernmost state, <a href='https://en.wikipedia.org/wiki/Rio_Grande_do_Sul'>Rio Grande do Sul.</a> The vast plains are a natural region, interrupted only by the low Ventana and Tandil hills, near Bahía Blanca and Tandil (Argentina), with a height of 1,300 m (4,265 ft) and 500 m (1,640 ft), respectively.</p>

         <p>The climate is temperate, with <a href='https://en.wikipedia.org/wiki/Precipitation'>precipitation</a> of 600 to 1,200 mm (23.6 to 47.2 in) that is more or less evenly distributed throughout the year, making the soils appropriate for <a href='https://en.wikipedia.org/wiki/Agriculture'>agriculture.</a> The area is also one of the distinct physiography provinces of the larger Paraná–Paraguay Plain division.</p>

         <div className='toFlexImages'>
            <div className='toCenterFigcaptionFitContent'><img src={MapPampas} alt='Cerrado Map' height={200} /><figcaption>Approximate location and borders of the Pampas encompassing the southeastern area of South America bordering the Atlantic Ocean.</figcaption></div>
            <div className='toCenterFigcaptionTextAlign'><img src={SerraGerall} alt='Landscape in the Pampas at eye level. Brazil' height={isDesktopOrLaptop ? 200 : 160} /><figcaption>Landscape in the Pampas at eye level. Brazil</figcaption></div>
         </div>

      </article>
   )
}

