import React from 'react';
import { useMediaQuery } from 'react-responsive';
import { NavLink } from 'react-router-dom';
import Description from '../MapBiomesImages/Description/Description.jsx';

import Pseudoseisura from './CaatingaSections/Pseudoseisura_cristata.jpg';
import DoAltoMontanha from './CaatingaSections/Do_alto_da_montanha.jpg';

export default function TheCaatinga() {

   const isDesktopOrLaptop = useMediaQuery({
      query: '(min-device-width: 1224px)'
   })

   React.useEffect(() => {
      document.title = 'Biomes of Brazil | The Caatinga';
   })

   return (
      <main id='main'>
         <h1>The Caatinga</h1>
         <Description />
         <h2>Choose the section you are interested in:</h2>
         <div className='toFlexImages'>
            <div className='toCenterFigcaptionTextAlign sectionRepresent'><NavLink exact to='/The Caatinga/CaatingaFauna' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}><img src={Pseudoseisura} alt='Common rock formations in the caatinga, during the rainy season' height={isDesktopOrLaptop ? 200 : 170} /><p>Fauna</p></NavLink></div>
            <div className='toCenterFigcaptionTextAlign sectionRepresent'><NavLink exact to='/The Caatinga/CaatingaFlora' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}><img src={DoAltoMontanha} alt='Pseudoseisura cristata, an endemic species.' height={isDesktopOrLaptop ? 200 : 170} /><p>Flora</p></NavLink></div>
         </div >
         <div className='videoContainer'>
            <h3>Watch video (in portugues)</h3>
            <iframe width={isDesktopOrLaptop ? 560 : 270} height={isDesktopOrLaptop ? 315 : 250} src="https://www.youtube.com/embed/JooQi_iT9cc" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
         </div>
         <br />
      </main >
   )
}