import React from 'react';
import { useMediaQuery } from 'react-responsive';

import '../../../../../commonStyles/sectionsStyles.scss';

import AnadenantheraColubrina from './PampaFloraImages/angico_vermelho.jpg';
import Araucaria from './PampaFloraImages/Araucaria.jpg';
import CyperusCompressus from './PampaFloraImages/barbaDeBode.jpg';
import Cactus from './PampaFloraImages/cactaceas.jpg';
import GuayaibiWood from './PampaFloraImages/Guajuvira.jpg';
import cordiaTrichotoma from './PampaFloraImages/LouroPardo.jpg';


export default function PampaFlora() {

   const isDesktopOrLaptop = useMediaQuery({
      query: '(min-device-width: 1224px)'
   })

   React.useEffect(() => {
      document.title = 'Brazil | The Pampa | Flora';
   })

   return <main id="main">
      <h1>Flora of the Pampa</h1>

      <p>Historically, frequent <a href='https://en.wikipedia.org/wiki/Wildfire'>wildfires</a> ensured that only small plants such as <a href='https://en.wikipedia.org/wiki/Poaceae'>grasses</a> flourished, while <a href='https://en.wikipedia.org/wiki/Tree'>trees</a> were less common. The dominant <a href='https://en.wikipedia.org/wiki/Vegetation'>vegetation</a> types are grassy <a href='https://en.wikipedia.org/wiki/Prairie'>prairie</a> and grass <a href='https://en.wikipedia.org/wiki/Steppe'>steppe</a>, in which numerous species of the grass genus <a href='https://en.wikipedia.org/wiki/Stipa'>Stipa</a> are particularly conspicuous. "Pampas grass" (<a href='https://en.wikipedia.org/wiki/Cortaderia_selloana'><em>Cortaderia selloana</em></a>) is an iconic species of the Pampas. Vegetation typically includes perennial grasses and herbs. Different strata of grasses occur because of gradients of water availability.</p>

      <p>The <a href='https://en.wikipedia.org/wiki/World_Wide_Fund_for_Nature'>World Wildlife Fund</a> divides the Pampas into three distinct ecoregions. The <a href='https://en.wikipedia.org/wiki/Uruguayan_savanna'>Uruguayan Savanna</a> lies east of the <a href='https://en.wikipedia.org/wiki/Paran%C3%A1_River'>Parana River</a>, and includes all of Uruguay, most of <a href='https://en.wikipedia.org/wiki/Entre_R%C3%ADos_Province'>Entre Rios</a> and <a href='https://en.wikipedia.org/wiki/Corrientes_Province'>Corrientes</a> provinces in Argentina, and the southern portion of Brazil's state of <a href='https://en.wikipedia.org/wiki/Rio_Grande_do_Sul'>Rio Grande do Sul</a>. The <a href='https://en.wikipedia.org/wiki/Humid_Pampas'>Humid Pampas</a> include eastern Buenos Aires Province, and southern Entre Rios Province. The <a href='https://en.wikipedia.org/wiki/Semi-arid_Pampas'>Semiarid Pampas</a> includes western Buenos Aires Province and adjacent portions of Santa Fe, Córdoba, and La Pampa provinces. The Pampas are bounded by the drier Argentine espinal grasslands, which form a semicircle around the north, west, and south of the Humid Pampas.</p>

      <p>Winters are cold to mild, and summers are hot and humid. Rainfall is fairly uniform throughout the year but is a little heavier during the summer. Annual rainfall is heaviest near the coast and decreases gradually further inland. Rain during the late spring and summer usually arrives in the form of brief heavy showers and thunderstorms. More general rainfall occurs the remainder of the year as <a href='https://en.wikipedia.org/wiki/Cold_front'>cold fronts</a> and storm systems move through. Although cold spells during the winter often send nighttime temperatures below freezing, <a href='https://en.wikipedia.org/wiki/Snow'>snow</a> is quite rare. In most winters, a few light snowfalls occur over inland areas.</p>

      <div className='toFlexImages'>
         <div className='toCenterFigcaptionTextAlign'><img src={AnadenantheraColubrina} alt='Anadenanthera Colubrina' height={200} /><figcaption>Anadenanthera Colubrina</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Araucaria} alt='Araucaria' height={isDesktopOrLaptop ? 200 : 160} /><figcaption>Araucaria</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={CyperusCompressus} alt='Cyperus Compressus(annual sedge)' height={200} /><figcaption>Cyperus Compressus(annual sedge)</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Cactus} alt='Cactus' height={200} /><figcaption>Cactus</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={GuayaibiWood} alt='Guayaibi Wood' height={isDesktopOrLaptop ? 200 : 180} /><figcaption>Guayaibi Wood</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={cordiaTrichotoma} alt='Cordia Trichotoma' height={200} /><figcaption>Cordia Trichotoma</figcaption></div>
      </div>
      <br />
   </main>
}
