import React from 'react';
import { useMediaQuery } from 'react-responsive';

import '../../../../../commonStyles/sectionsStyles.scss';

import Cereus from './PantanalFloraImages/Cereus_jamacaru.JPG';
import Ficus from './PantanalFloraImages/Figueiras.jpg';
import Arecaceae from './PantanalFloraImages/Manila_dwarf_coconut_palm.jpg';
import Orchidaceae from './PantanalFloraImages/Orchis_militaris_flowers.jpg';
import Handroanthus from './PantanalFloraImages/Tabebuia_avellanedae.jpg';
import Victoria from './PantanalFloraImages/vitoriaRegia.jpg';

export default function PantanalFlora() {

   const isDesktopOrLaptop = useMediaQuery({
      query: '(min-device-width: 1224px)'
   })

   React.useEffect(() => {
      document.title = 'Brazil | The Pantanal | Flora';
   })

   return <main id="main">
      <h1>Flora of the Pantanal</h1>

      <p>The vegetation of the Pantanal, often referred to as the "Pantanal <a href='https://en.wikipedia.org/wiki/Complex_system'>complex</a>", is a mixture of plant communities typical of a variety of surrounding <a href='https://en.wikipedia.org/wiki/Biome'>biome</a> regions: these include moist tropical Amazonian rainforest plants, semiarid woodland plants typical of northeast Brazil, Brazilian <a href='https://en.wikipedia.org/wiki/Cerrado'><em>cerrado</em></a> savanna plants and plants of the <em>Chaco</em> savannas of Bolivia and Paraguay. Forests usually occur at higher altitudes of the region, while grasslands cover the seasonally inundated areas. The key limiting factors for growth are inundation and, even more importantly, water-stress during the dry season.</p>

      <p>According to <a href='https://en.wikipedia.org/wiki/Brazilian_Agricultural_Research_Corporation'>Embrapa</a>, approximately 2,000 different plants have been identified in the Pantanal biome and classified according to their potential, with some presenting significant medicinal promise.</p>
      <div className='toFlexImages'>
         <div className='toCenterFigcaptionTextAlign'><img src={Cereus} alt='Cereus jamacaru' height={200} /><figcaption>Cereus jamacaru</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Ficus} alt='Ficus organensis' height={isDesktopOrLaptop ? 200 : 180} /><figcaption>Ficus organensis</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Arecaceae} alt='Arecaceae' height={200} /><figcaption>Arecaceae</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Orchidaceae} alt='Orchidaceae' height={200} /><figcaption>Orchidaceae</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Handroanthus} alt='Androanthus impetiginosus or pink ipê' height={200} /><figcaption>Androanthus impetiginosus</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Victoria} alt='Victoria regia' height={isDesktopOrLaptop ? 200 : 180} /><figcaption>Victoria regia</figcaption></div>
      </div>
      <br />
   </main>
}

