import React from 'react';
import ReactDOM from 'react-dom';
import { Route, Switch, HashRouter as Router } from "react-router-dom";

import './index.scss';

import TheAmazonBiome from './App/Main/MapBiomes/TheAmazonBiome/TheAmazonBiome.jsx';
import TheAtlanticForest from './App/Main/MapBiomes/TheAtlanticForest/TheAtlanticForest.jsx';
import TheCaatinga from './App/Main/MapBiomes/TheCaatinga/TheCaatinga.jsx';
import TheCerrado from './App/Main/MapBiomes/TheCerrado/TheCerrado.jsx';
import ThePampa from './App/Main/MapBiomes/ThePampa/ThePampa.jsx';
import ThePantanal from './App/Main/MapBiomes/ThePantanal/ThePantanal.jsx';

import AmazonFauna from './App/Main/MapBiomes/TheAmazonBiome/AmazonSections/AmazonFauna.jsx';
import AmazonFlora from './App/Main/MapBiomes/TheAmazonBiome/AmazonSections/AmazonFlora.jsx';
import AtlanticFauna from './App/Main/MapBiomes/TheAtlanticForest/AtlanticSections/AtlanticFauna.jsx';
import AtlanticFlora from './App/Main/MapBiomes/TheAtlanticForest/AtlanticSections/AtlanticFlora.jsx';
import CaatingaFauna from './App/Main/MapBiomes/TheCaatinga/CaatingaSections/CaatingaFauna.jsx';
import CaatingaFlora from './App/Main/MapBiomes/TheCaatinga/CaatingaSections/CaatingaFlora.jsx';
import CerradoFauna from './App/Main/MapBiomes/TheCerrado/CerradoSections/CerradoFauna.jsx';
import CerradoFlora from './App/Main/MapBiomes/TheCerrado/CerradoSections/CerradoFlora.jsx';
import PampaFauna from './App/Main/MapBiomes/ThePampa/PampaSections/PampaFauna.jsx';
import PampaFlora from './App/Main/MapBiomes/ThePampa/PampaSections/PampaFlora.jsx';
import PantanalFauna from './App/Main/MapBiomes/ThePantanal/PantanalSections/PantanalFauna.jsx';
import PantanalFlora from './App/Main/MapBiomes/ThePantanal/PantanalSections/PantanalFlora.jsx';

import Header from './App/Header/Header.jsx';
import Sidebar from './App/Sidebar/Sidebar.jsx';
import Main from './App/Main/Main.jsx';
import Footer from './App/Footer/Footer.jsx';
import NotFound from './App/NotFound/NotFound.jsx';
import Contact from './App/Sidebar/Contact/Contact.jsx';
import AboutThisSite from './App/Sidebar/AboutThisSite/AboutThisSite.jsx';
import Contents from './App/Sidebar/Contents/Contents.jsx';

ReactDOM.render(
   <Router>
      <Header />
      <div className='toFlex wrapperForFooter'>
         <Switch>
            <Route exact path="/" component={Main} />
            <Route exact path="/The Amazon Rainforest" component={TheAmazonBiome} />
            <Route exact path="/The Atlantic Forest" component={TheAtlanticForest} />
            <Route exact path="/The Caatinga" component={TheCaatinga} />
            <Route exact path="/The Cerrado" component={TheCerrado} />
            <Route exact path="/The Pampa" component={ThePampa} />
            <Route exact path="/The Pantanal" component={ThePantanal} />

            <Route exact path="/The Amazon Rainforest/AmazonFauna" component={AmazonFauna} />
            <Route exact path="/The Amazon Rainforest/AmazonFlora" component={AmazonFlora} />
            <Route exact path="/The Atlantic Forest/AtlanticFauna" component={AtlanticFauna} />
            <Route exact path="/The Atlantic Forest/AtlanticFlora" component={AtlanticFlora} />
            <Route exact path="/The Caatinga/CaatingaFauna" component={CaatingaFauna} />
            <Route exact path="/The Caatinga/CaatingaFlora" component={CaatingaFlora} />
            <Route exact path="/The Cerrado/CerradoFauna" component={CerradoFauna} />
            <Route exact path="/The Cerrado/CerradoFlora" component={CerradoFlora} />
            <Route exact path="/The Pampa/PampaFauna" component={PampaFauna} />
            <Route exact path="/The Pampa/PampaFlora" component={PampaFlora} />
            <Route exact path="/The Pantanal/PantanalFauna" component={PantanalFauna} />
            <Route exact path="/The Pantanal/PantanalFlora" component={PantanalFlora} />

            <Route exact path="/Contact" component={Contact} />
            <Route exact path="/About this site" component={AboutThisSite} />
            <Route exact path="/Contents" component={Contents} />
            <Route component={NotFound} />
         </Switch>
         <Sidebar />
         <div className='pushFooter'></div>
      </div>
      <Footer />
   </Router>,
   document.getElementById('root')
);

