import React from 'react';
import { NavLink } from 'react-router-dom';

import './Sidebar.scss';
import { timer, animate } from '../../animations/animate';

export default function Sidebar() {
   const colorStyle = {
      color: 'green'
   }

   React.useEffect(() => {
      let el = document.querySelector('#unit');
      if (el) {
         clearInterval(timer);
         el.remove();
      }
      animate()
   }, []);

   const [anim, setAnim] = React.useState(true);

   const turnOnOffAnimation = function (prevState) {
      setAnim(!anim)
      if (anim) {
         clearInterval(timer);
         document.querySelector('#unit').remove();
      } else {
         animate()
      }
   }

   return (
      <nav id="sidebar">
         <p><NavLink exact to='/' activeStyle={{ color: '#32CD32' }} style={colorStyle}>Home</NavLink></p>
         <p><NavLink exact to='/Contents' activeStyle={{ color: '#32CD32' }} style={colorStyle}>Contents</NavLink></p>
         <p><NavLink exact to='/About this site' activeStyle={{ color: '#32CD32' }} style={colorStyle}>About this site</NavLink></p>
         <p><NavLink exact to='/Contact' activeStyle={{ color: '#32CD32' }} style={colorStyle}>Contact</NavLink></p>
         <p id='animButton' onClick={turnOnOffAnimation}>{anim ? 'Turn off animations' : 'Turn on animations'}</p>
      </nav>
   )
}

