import React from 'react';

import TheAtlanticForestSerra from './TheAtlanticForestSerra.jpg';
import AtlanticForestWWF from './Atlantic_Forest_WWF.jpg';

export default class TheAtlanticForestDescr extends React.Component {
   render() {
      return (
         <article id="description">
            <p><b>The Atlantic Forest</b> (Portuguese: Mata Atlântica) is a South American forest that extends along the <a href='https://en.wikipedia.org/wiki/Atlantic_Ocean'>Atlantic</a> coast of <a href='https://en.wikipedia.org/wiki/Brazil'>Brazil</a> from Rio Grande do Norte state in the northeast to Rio Grande do Sul state in the south and inland as far as <a href='https://en.wikipedia.org/wiki/Paraguay'>Paraguay</a> and the Misiones Province of <a href='https://en.wikipedia.org/wiki/Argentina'>Argentina</a>, where the region is known as <b>Selva Misionera.</b></p>

            <p>The Atlantic Forest has <a href='https://en.wikipedia.org/wiki/Ecoregion#Terrestrial'>ecoregions</a> within the following biome categories: seasonal moist and dry broad-leaf tropical forests, tropical and subtropical grasslands, savannas, and shrublands, and mangrove forests. The Atlantic Forest is characterized by a high biodiversity and endemism.</p>

            <p>It was the first environment that the Portuguese colonists encountered over 500 years ago, when it was thought to have had an area of 1,000,000–1,500,000 km2 (390,000–580,000 sq mi), and stretching an unknown distance inland. Over 85% of the original area has been deforested, threatening many plant and animal species with <a href='https://en.wikipedia.org/wiki/Extinction'>extinction.</a></p>

            <div className='toFlexImages'>
               <div className='toCenterFigcaptionTextAlign'><img src={TheAtlanticForestSerra} alt='Atlantic Forest' height={200} /><figcaption>Area of the Atlantic Forest in Serra do Mar</figcaption></div>

               <div className='toCenterFigcaptionFitContent'><img src={AtlanticForestWWF} alt='Atlantic Forest Map' height={200} /><figcaption>Map of the Atlantic Forest ecoregions as delineated by the WWF. The yellow line approximately encloses the forest's distribution.</figcaption></div>
            </div>
         </article >
      )
   }
}