import React from 'react';
import { NavLink } from 'react-router-dom';
import { useMediaQuery } from 'react-responsive';

import Description from '../MapBiomesImages/Description/Description.jsx';

import PhysalaemusNattereriInDeimaticBehavior from './CerradoSections/Physalaemus_nattereri_in_deimatic_behavior.jpg';
import Cerrado from './CerradoSections/Cerrado.jpg';

export default function TheCerrado(props) {

   const isDesktopOrLaptop = useMediaQuery({
      query: '(min-device-width: 1224px)'
   })

   React.useEffect(() => {
      document.title = 'Biomes of Brazil | The Cerrado';
   })

   return (
      <main id='main'>
         <h1>The Cerrado</h1>
         <Description />
         <h2>Choose an interested section:</h2>
         <div className='toFlexImages'>
            <div className='toCenterFigcaptionFitContent sectionRepresent'><NavLink exact to='/The%20Cerrado/CerradoFauna' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}><img src={PhysalaemusNattereriInDeimaticBehavior} alt='Physalaemus nattereri in deimatic behavior' height={200} /><p>Fauna</p></NavLink></div>
            <div className='toCenterFigcaptionFitContent sectionRepresent'><NavLink exact to='/The%20Cerrado/CerradoFlora' activeStyle={{ color: '#32CD32' }} style={{ color: '#32CD32' }}><img src={Cerrado} alt='Cerrado' height={isDesktopOrLaptop ? 200 : 180} /><p>Flora</p></NavLink></div>
         </div>
         <div className='videoContainer'>
            <h3>Watch video (in portugues)</h3>
            <iframe width={isDesktopOrLaptop ? 560 : 270} height={isDesktopOrLaptop ? 315 : 250} src="https://www.youtube.com/embed/788WH2ctu90" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
         </div>
         <br />
      </main>
   )
}

