import React from 'react';
import '../../../../../commonStyles/sectionsStyles.scss';

import Fabaceae from './CerradoFloraImages/Flowering_kudzu.jpg';
import Rubiaceae from './CerradoFloraImages/Luculia_gratissima.jpg';
import GalleryForests from './CerradoFloraImages/Mara_River_Massai_Mara.jpg';
import Meadow from './CerradoFloraImages/Meadow_Foxtail_head.jpg';
import Moss from './CerradoFloraImages/Moos_mit_kleinem_Baumstumpf.jpg';
import Myrtus from './CerradoFloraImages/Myrtus_communis.jpg';
import Cyperaceae from './CerradoFloraImages/Pycreus.jpg'
import Qualea from './CerradoFloraImages/Qualea_dichotoma.jpg';
import Malpighiaceae from './CerradoFloraImages/Starr_080117-1999_Galphimia_gracilis.jpg';
import Tibouchina from './CerradoFloraImages/Tibouchina_semidecandra.jpg';

export default function CerradoFlora() {

   React.useEffect(() => {
      document.title = 'Brazil | The Cerrado | Flora';
   })

   return <main id="main">
      <p>The Cerrado is characterized by unique vegetation types. It is composed of a shifting mosaic of habitats, with the savanna-like <em>cerrado</em> itself on well-drained areas between strips of <a href='https://en.wikipedia.org/wiki/Gallery_forest'>gallery forest</a> (closed canopy tall forest) which occur along streams. Between the <em>cerrado</em> and the gallery forest is an area of vegetation known as the wet <em>campo</em> with distinct up- and downslope borders where tree growth is inhibited due to wide seasonal fluctuations in the water table.</p>

      <p>The savanna portion of the Cerrado is heterogeneous in terms of canopy cover. Goodland (1971) divided the Cerrado into four categories ranging from least to most canopy cover: <em>campo sujo</em> (<a href='https://en.wikipedia.org/wiki/Stratification_(vegetation)#Herb_layer'>herbaceous layer</a> with occasional small trees about 3 m tall), <em>campo cerrado</em> (slightly higher density of trees about 4 m tall on average), <em>cerrado sensu stricto</em> (orchard-like vegetation with trees about 6 m high) and <a href='https://en.wikipedia.org/wiki/Cerrad%C3%A3o'><em>cerradao</em></a> (canopy cover near 50% with general height 9 m).</p>

      <p>Probably around 800 species of trees are found in the Cerrado. Among the most diverse families of trees in the Cerrado are the <a href='https://en.wikipedia.org/wiki/Fabaceae'>Leguminosae</a> , <a href='https://en.wikipedia.org/wiki/Malpighiaceae'>Malpighiaceae</a> (46), <a href='https://en.wikipedia.org/wiki/Myrtaceae'>Myrtaceae</a> (43), <a href='https://en.wikipedia.org/wiki/Melastomataceae'>Melastomataceae</a> (32) and <a href='https://en.wikipedia.org/wiki/Rubiaceae'>Rubiaceae</a> (30). Much of the Cerrado is dominated by the <a href='https://en.wikipedia.org/wiki/Vochysiaceae'>Vochysiaceae</a> (23 species in the Cerrado) due to the abundance of three species in the genus <a href='https://en.wikipedia.org/wiki/Qualea'>Qualea</a>. The herbaceous layer usually reaches about 60 cm in height and is composed mainly of the <a href='https://en.wikipedia.org/wiki/Poaceae'>Poaceae</a>, <a href='https://en.wikipedia.org/wiki/Cyperaceae'>Cyperaceae</a>, <a href='https://en.wikipedia.org/wiki/Fabaceae'>Leguminosae</a>, <a href='https://en.wikipedia.org/wiki/Asteraceae'>Compositae</a>, <a href='https://en.wikipedia.org/wiki/Myrtaceae'>Myrtaceae</a> and <a href='https://en.wikipedia.org/wiki/Rubiaceae'>Rubiaceae</a>. Much of the vegetation in the gallery forests is similar to nearby rainforest; however, there are some endemic species found only in the Cerrado gallery forests.</p>

      <p>Soil fertility, fire regime and hydrology are thought to be most influential in determining Cerrado vegetation. Cerrado soils are always well-drained and most are <a href='https://en.wikipedia.org/wiki/Oxisol'>oxisols</a> with low pH and low calcium and magnesium. The amount of potassium, nitrogen and phosphorus has been found to be positively correlated with tree trunk basal area in Cerrado habitats. Much as in other grasslands and savannas, fire is important in maintaining and shaping the Cerrado's landscape; many plants in the Cerrado are fire-adapted, exhibiting characters like thick corky bark to withstand the heat.</p>

      <p>Cerrado vegetation is believed to be ancient, stretching back perhaps as far in a prototypic form as the <a href='https://en.wikipedia.org/wiki/Cretaceous'>Cretaceous</a>, before Africa and South America separated. A dynamic expansion and contraction between cerrado and Amazonian rainforest has probably occurred historically, with expansion of the Cerrado during glacial periods like the <a href='https://en.wikipedia.org/wiki/Pleistocene'>Pleistocene</a>. These processes and the resulting fragmentation in multiple <a href='https://en.wikipedia.org/wiki/Refugium_(population_biology)'>refugia</a> have probably contributed to the high <a href='https://en.wikipedia.org/wiki/Species_richness'>species richness</a> both of the Cerrado and of the Amazonian rainforest.</p>

      <div className='toFlexImages'>
         <div className='toCenterFigcaptionTextAlign'><img src={Fabaceae} alt='Fabaceae' height={200} /><figcaption>Fabaceae</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Rubiaceae} alt='Rubiaceae' height={200} /><figcaption>Rubiaceae</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={GalleryForests} alt='Gallery forests in the Masai Mara' height={200} /><figcaption>Gallery forests in the Masai Mara</figcaption></div>
         <div className='toCenterFigcaptionFitContent'><img src={Meadow} alt='Flowering head of meadow foxtail (Alopecurus pratensis), with stamens exerted at anthesis' height={200} /><figcaption>Flowering head of meadow foxtail (Alopecurus pratensis), with stamens exerted at anthesis</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Moss} alt='Moss layer on the forest floor' height={200} /><figcaption>Moss layer on the forest floor</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Myrtus} alt='Myrtus communis foliage and flowers' height={200} /><figcaption>Myrtus communis foliage and flowers</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Cyperaceae} alt='Cyperaceae' height={200} /><figcaption>Cyperaceae</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Qualea} alt='Qualea dichotoma' height={200} /><figcaption>Qualea dichotoma</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Malpighiaceae} alt='Malpighiaceae' height={200} /><figcaption>Malpighiaceae</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Tibouchina} alt='Tibouchina semidecandra ' height={200} /><figcaption>Tibouchina semidecandra </figcaption></div>
      </div>
      <br />
   </main>
}


