import React from 'react';
import { useMediaQuery } from 'react-responsive';

import '../../../../../commonStyles/sectionsStyles.scss';

import Howler from './PantanalFaunaImages/Black_Howler.jpg';
import Caiman from './PantanalFaunaImages/Caiman_yacare_Pantanal.jpg';
import Capybaras from './PantanalFaunaImages/Hydrochoerus_hydrochaeris.jpg';
import Jaguar from './PantanalFaunaImages/jaguar.jpg';
import Anteater from './PantanalFaunaImages/Myrmecophaga.jpg';
import Tapir from './PantanalFaunaImages/Tapir_Pantanal.jpg';

export default function PantanalFauna() {

   const isDesktopOrLaptop = useMediaQuery({
      query: '(min-device-width: 1224px)'
   })

   React.useEffect(() => {
      document.title = 'Brazil | The Pantanal | Fauna';
   })

   return <main id="main">
      <h1>Fauna of the Pantanal</h1>

      <p>The Pantanal <a href='https://en.wikipedia.org/wiki/Ecosystem'>ecosystem</a> is also thought to be home to 463 bird species, 269 fish species, more than 236 mammalian species, 141 reptile and amphibian species, and over 9,000 subspecies of invertebrates.</p>

      <p>The <a href='https://en.wikipedia.org/wiki/Ampullariidae'>apple snail</a> is a <a href='https://en.wikipedia.org/wiki/Keystone_species'>keystone species</a> in Pantanal's ecosystem. When the wetlands are flooded once a year, the grass and other plants will eventually die and start to decay. During this process, decomposing microbes deplete the shallow water of all oxygen, suffocating larger decomposers. Unlike other decomposing animals, the apple snails have both gills and lungs, making it possible for them to thrive in <a href='https://en.wikipedia.org/wiki/Anoxic_waters'>anoxic waters</a> where they recycle the nutrients. To get oxygen, they extend a long snorkel to the water surface, pumping air into their lungs. This ability allows them to consume all the dead plant matter and turn it into nutritious fertilizer available for the plants in the area. The snails themselves are also food for a variety of animals.</p>

      <p>Among the rarest animals to inhabit the wetland of the Pantanal are the <a href='https://en.wikipedia.org/wiki/Marsh_deer'>marsh deer</a> (<em>Blastocerus dichotomus</em>) and the <a href='https://en.wikipedia.org/wiki/Giant_otter'>giant river otter</a> (<em>Pteronura brasiliensis</em>). Parts of the Pantanal are also home to the following endangered or threatened species: the <a href='https://en.wikipedia.org/wiki/Hyacinth_macaw'>hyacinth macaw</a> (<em>Anodorhyncus hyacinthinus</em>) (a bird endangered due to smuggling), the <a href='https://en.wikipedia.org/wiki/Crowned_solitary_eagle'>crowned solitary eagle</a>, the <a href='https://en.wikipedia.org/wiki/Maned_wolf'>maned wolf</a> (<em>Chrysocyon brachyurus</em>), the <a href='https://en.wikipedia.org/wiki/Bush_dog'>bush dog</a> (<em>Speothos venaticus</em>), <a href='https://en.wikipedia.org/wiki/South_American_tapir'>the South American tapir</a> (<em>Tapirus terrestris</em>) and the <a href='https://en.wikipedia.org/wiki/Giant_anteater'>giant anteater</a> (<em>Myrmecophaga tridactyla</em>). Common species in the Pantanal include the <a href='https://en.wikipedia.org/wiki/Capybara'>capybara</a> (<em>Hydrochoerus hydrochaeris</em>), <a href='https://en.wikipedia.org/wiki/Ocelot'>Ocelot</a> (<em>Leopardus pardalis</em>) and the <a href='https://en.wikipedia.org/wiki/Yacare_caiman'>yacare caiman</a> (<em>Caiman yacare</em>). According to 1996 data, there were 10 million caimans in the Pantanal, making it the highest concentration of crocodilians in the World. The Pantanal is home to one of the largest and healthiest <a href='https://en.wikipedia.org/wiki/Jaguar'>jaguar</a> (<em>Panthera onca</em>) populations on Earth.</p>

      <p>There are thirteen species of herons and <a href='https://en.wikipedia.org/wiki/Egret'>egrets</a>, six species of <a href='https://en.wikipedia.org/wiki/Ibis'>ibises</a> and <a href='https://en.wikipedia.org/wiki/Spoonbill'>spoonbills</a>, and five species of <a href='https://en.wikipedia.org/wiki/Kingfisher'>kingfishers</a> that use the Pantanal as a breeding and feeding ground. There are nineteen species of <a href='https://en.wikipedia.org/wiki/Parrot'>parrots</a> documented in the Pantanal, including five species of <a href='https://en.wikipedia.org/wiki/Macaw'>macaws</a>. Some migratory birds include the <a href='https://en.wikipedia.org/wiki/American_golden_plover'>American golden plover</a>, <a href='https://en.wikipedia.org/wiki/Peregrine_falcon'>Peregrine falcon</a>, and the <a href='https://en.wikipedia.org/wiki/Bobolink'>Bobolink</a>.</p>

      <p>Most <a href='https://en.wikipedia.org/wiki/Fish'>fish</a> are <a href='https://en.wikipedia.org/wiki/Detritivore'>detritivores</a>, primarily ingesting fine particles from sediments and plant surfaces. This is characteristic of fish living in South American flood-plains in general. Fish migration between river channels and flood-plain regions occurs seasonally. These fish have many <a href='https://en.wikipedia.org/wiki/Adaptation'>adaptations</a> that allow them to survive in the oxygen-depleted flood-plain waters.</p>

      <p>In addition to the <a href='https://en.wikipedia.org/wiki/Caiman'>caiman</a>, some of the reptiles that inhabit the Pantanal are the <a href='https://en.wikipedia.org/wiki/Yellow_anaconda'>yellow anaconda</a> (<em>Eunectes notaeus</em>), the <a href='https://en.wikipedia.org/wiki/Gold_tegu'>gold tegu</a> (<em>Tupinambis teguixin</em>), the <a href='https://en.wikipedia.org/wiki/Red-footed_tortoise'>red-footed tortoise</a> (<em>Geochelone carbonaria</em>) and the <a href='https://en.wikipedia.org/wiki/Green_iguana'>green iguana</a> (<em>Iguana iguana</em>).</p>

      <div className='toFlexImages'>
         <div className='toCenterFigcaptionTextAlign'><img src={Howler} alt='Black howler monkeys' height={isDesktopOrLaptop ? 200 : 180} /><figcaption>Black howler monkeys</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Caiman} alt='Yacare caiman' height={isDesktopOrLaptop ? 200 : 120} /><figcaption>Yacare caiman</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Capybaras} alt='Capybaras' height={isDesktopOrLaptop ? 200 : 180} /><figcaption>Capybaras</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Jaguar} alt='Pantanal jaguar' height={isDesktopOrLaptop ? 200 : 190} /><figcaption>Pantanal jaguar</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Anteater} alt='Giant anteater' height={200} /><figcaption>Giant anteater</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={Tapir} alt='Lowland tapir' height={isDesktopOrLaptop ? 200 : 180} /><figcaption>Lowland tapir</figcaption></div>
      </div>
      <br />
   </main>
}


