import React from 'react';

import Cerrado from './Cerrado.jpg';
import CerradoMap from './CerradoMap.png';
import CerradoVegetation from './CerradoVegetation.jpg';

export default class TheCerradoDescr extends React.Component {

   render() {
      return (
         <article id="description">
            <p>The <b>Cerrado</b> is a vast <a href='https://en.wikipedia.org/wiki/Tropics'>tropical</a> savanna ecoregion of Brazil, particularly in the states of Goiás, Mato Grosso do Sul, Mato Grosso, Tocantins, Minas Gerais and the <a href='https://en.wikipedia.org/wiki/Federal_District_(Brazil)'>Federal District</a>. The core areas of the Cerrado biome are the Brazilian highlands, the Planalto. The main habitat types of the Cerrado consist of forest savanna, wooded savanna, park savanna and gramineous-woody savanna. Savanna wetlands and gallery forests are also included. The second largest of Brazil's major habitat types, after the Amazonian rainforest, the Cerrado accounts for a full 21 percent of the country's land area (extending marginally into Paraguay and Bolivia).</p>

            <p>The first detailed European account of the Brazilian cerrados was provided by Danish botanist Eugenius Warming (1892) in the book Lagoa Santa, in which he describes the main features of the cerrado vegetation in the state of Minas Gerais.</p>

            <p>Since then vast amounts of research have proved that the Cerrado is one of the richest of all tropical savanna regions and has high levels of endemism. Characterizing it by its enormous ranges of plant and animal biodiversity, <a href='https://en.wikipedia.org/wiki/World_Wide_Fund_for_Nature'>World Wide Fund for Nature</a> named the Cerrado the biologically richest savanna in the world, with about 10,000 plant species and 10 endemic bird species. There are nearly 200 species of mammal in the Cerrado, though only 14 are endemic.</p>

            <div className='toFlexImages'>
               <div className='toCenterFigcaptionFitContent'><img src={CerradoVegetation} alt='Map of Cerrado' height={200} /><figcaption>Map of the Atlantic Forest ecoregions as delineated by the WWF. The yellow line approximately encloses the forest's distribution.</figcaption></div>
               <div className='toCenterFigcaptionFitContent'><img src={CerradoMap} alt='Cerrado Map' height={200} /><figcaption>Map of the Cerrado ecoregion as delineated by the World Wide Fund for Nature.</figcaption></div>
               <div className='toCenterFigcaptionFitContent'><img src={Cerrado} alt='Cerrado Camp Vegetation' height={200} /><figcaption>Vegetation in northwest Minas Gerais, Brazil.</figcaption></div>
            </div>

         </article >
      )
   }
}




