import React from 'react';
import '../../../../../commonStyles/sectionsStyles.scss';

import aphelandraSquarrosa from './AtlanticFloraImages/aphelandra-squarrosa.jpg';
import BegoniaCoccinea from './AtlanticFloraImages/Begonia_coccinea.jpg';
import TillandsiaGeminiflora from './AtlanticFloraImages/Tillandsia_geminiflora.jpg';
import TillandsiaStricta from './AtlanticFloraImages/Tillandsia_stricta.JPG';
import VrieseaCarinata from './AtlanticFloraImages/Vriesea_Carinata.jpg';
import VrieseaCieroglyphica from './AtlanticFloraImages/Vriesea_Hieroglyphica.jpg';

export default function AtlanticFlora() {

   React.useEffect(() => {
      document.title = 'Brazil | The Atlantic Forest | Flora';
   })

   return <main id="main">
      <h1>Flora of the Atlantic Forest</h1>
      <p>The forest is also home to around 20,000 species of <a href='https://en.wikipedia.org/wiki/Plant'>plants</a>, and new species continue to be discovered. The Amazon Institute is active in reforestation efforts in the northeastern state of Pernambuco, <a href='https://en.wikipedia.org/wiki/Brazil'>Brazil</a>. During 2007, 5,500 new <a href='https://en.wikipedia.org/wiki/Tree'>trees</a> were planted in the mountains, adding to what little remains of the ancient forest. The Nature Conservancy, its Brazilian counterpart, and several corporations are involved in reforestation and <a href='https://en.wikipedia.org/wiki/Forest'>forest</a> management projects in the south.</p>

      <p>During <a href='https://en.wikipedia.org/wiki/Glacial_period'>glacial periods</a>, however, the Atlantic Forest is known to have shrunk to extremely small refuges in highly sheltered gullies, with most of the land area being occupied by dry forest or even semi-<a href='https://en.wikipedia.org/wiki/Desert'>desert</a>. Some maps even suggest the forest actually survived in moist pockets well away from the coastline, where its endemic <a href='https://en.wikipedia.org/wiki/Rainforest'>rainforest</a> species mixed with much cooler-climate species.</p>
      <div className='toFlexImages'>
         <div className='toCenterFigcaptionFitContent'><img src={aphelandraSquarrosa} alt='Aphelandra Squarrosa' height={200} /><figcaption>Aphelandra Squarrosa</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={BegoniaCoccinea} alt='Begonia Coccinea' height={200} /><figcaption>Begonia Coccinea</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={TillandsiaGeminiflora} alt='Tillandsia Geminiflora' height={200} /><figcaption>Tillandsia Geminiflora</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={TillandsiaStricta} alt='Tillandsia Stricta' height={200} /><figcaption>Tillandsia Stricta</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={VrieseaCarinata} alt='Vriesea Carinata' height={200} /><figcaption>Vriesea Carinata</figcaption></div>
         <div className='toCenterFigcaptionTextAlign'><img src={VrieseaCieroglyphica} alt='Vriesea Cieroglyphica' height={200} /><figcaption>Vriesea Cieroglyphica</figcaption></div>
      </div>
      <br />
   </main>
}
